import React, { useEffect, useState } from "react";
import Box from "@mui/material/Box";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { Header } from "~/components/manage/Header";
import EditSurveyGeneral from "~/components/manage/EditSurveyGeneral";
import SurveyService from "~/services/SurveyService";
import styles from "./EditSurvey.module.css";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import {
  onError,
  onErrorSeen,
  permissionsLoading,
  permissionsReceived,
  surveyReceived,
} from "~/state/edit/editState";
import { CircularProgress } from "@mui/material";
import EditingSurvey from "~/components/manage/EditingSurvey";
import SurveySharing from "~/components/manage/SurveySharing";
import SurveyOffline from "~/components/manage/SurveyOffline";
import SurveyPrivacy from "~/components/manage/SurveyPrivacy";
import SurveyUsers from "~/components/manage/SurveyUsers";
import SurveyQuota from "~/components/manage/SurveyQuota";
import { useSelector } from "react-redux";
import ProcessedError from "~/components/manage/ProcessedError";
import { PROCESSED_ERRORS, processError } from "~/utils/errorsProcessor";

function EditSurvey() {
  const { t } = useTranslation("manage");
  const dispatch = useDispatch();
  const [fetching, setFetching] = useState(true);

  const survey = useSelector((state) => state.editState.survey);

  const sections = [
    {
      id: "general",
      title: t("edit_survey.general"),
      component: <EditSurveyGeneral />,
    },
    {
      id: "sharing",
      title: t("edit_survey.sharing"),
      component: <SurveySharing />,
    },
    {
      id: "offline",
      title: t("edit_survey.offline"),
      component: <SurveyOffline />,
    },
    {
      id: "quotas",
      title: t("edit_survey.quotas"),
      component: <SurveyQuota />,
    },
    {
      id: "survey_users",
      title: t("edit_survey.survey_users"),
      component: <SurveyUsers />,
    },
    {
      id: "privacy",
      title: t("edit_survey.privacy"),
      component: <SurveyPrivacy />,
    },
  ];

  const processApirror = (e) => {
    setFetching(false);
    const processed = processError(e);
    switch (processed) {
      case PROCESSED_ERRORS.NETWORK_ERR:
      case PROCESSED_ERRORS.BACKEND_DOWN:
        dispatch(onError(processed));
        break;
      default:
        break;
    }
  };

  const error = useSelector((state) => state.editState.error);

  useEffect(() => {
    setFetching(true);
    SurveyService.getSurvey()
      .then((data) => {
        if (data) {
          dispatch(surveyReceived(data));
          loadPermissions(data.id);
        }
        setFetching(false);
      })
      .catch((err) => {
        processApirror(err);
      });

    dispatch(permissionsLoading(true));
  }, []);

  const loadPermissions = (surveyId) => {
    SurveyService.getSurveyPermissionAll(surveyId)
      .then((data) => {
        if (data) {
          dispatch(permissionsReceived(data));
        }
        dispatch(permissionsLoading(false));
      })
      .catch((err) => {
        processApirror(err);
      });
  };

  return (
    <Box className={styles.mainContainer}>
      {error && (
        <ProcessedError
          error={error}
          handleClose={() => dispatch(onErrorSeen())}
        />
      )}
      {!fetching && survey &&
        sections.map((section) => {
          return (
            <Accordion key={section.id} defaultExpanded={false}>
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography variant="h5">{section.title}</Typography>
              </AccordionSummary>
              <AccordionDetails>{section.component}</AccordionDetails>
            </Accordion>
          );
        })}
      {fetching && (
        <div className={styles.loadingWrapper}>
          <CircularProgress />
        </div>
      )}
      <EditingSurvey />
    </Box>
  );
}

export default EditSurvey;
