import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector, useDispatch } from "react-redux";
import {
  Box,
  CircularProgress,
  Typography,
  FormControlLabel,
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Stack,
  Button,
  TextField,
  Backdrop,
} from "@mui/material";
import {
  permissionsLoading,
  permissionsReceived,
  surveyAttributeChanged,
} from "~/state/edit/editState";
import SurveyService from "~/services/SurveyService";
import { isSurveyAdmin, isSurveyor } from "~/constants/roles";
import { SURVEY_STATUS } from "~/constants/survey";
import styles from "./SurveyQuota.module.css";

function SurveyQuota() {
  const dispatch = useDispatch();
  const { t } = useTranslation("manage");
  const survey = useSelector((state) => state.editState.survey);

  const [checked, setChecked] = useState(survey.quota > 0);
  const [limit, setLimit] = useState(survey.quota >= 0 ? survey.quota : "");

  const changeLimit = (value) => {
    const intValue = parseInt(value);
    if (Number.isInteger(intValue) && intValue >= 0) {
      setLimit(intValue);
      dispatch(
        surveyAttributeChanged({
          key: "quota",
          value: intValue,
        })
      );
    }
  };

  const fetching = useSelector((state) => state.editState.permissionsLoading);
  const permissionAll = useSelector((state) =>
    state.editState.permissions?.filter((permission) => {
      return isSurveyor(permission.user);
    })
  );

  const onChangeCheckbox = (e) => {
    if (!e.target.checked) {
      setLimit("");
      dispatch(
        surveyAttributeChanged({
          key: "quota",
          value: -1,
        })
      );
    }
    setChecked(e.target.checked);
  };

  const putPermissionBySurveyor = (userId, quota) => {
    const data = {
      quota,
    };
    dispatch(permissionsLoading(true));
    SurveyService.editPermission(survey.id, userId, data)
      .then(() => {
        loadPermissions(survey.id);
      })
      .catch((err) => {
        dispatch(permissionsLoading(false));
      });
  };

  const loadPermissions = (surveyId) => {
    SurveyService.getSurveyPermissionAll(surveyId)
      .then((data) => {
        if (data) {
          dispatch(permissionsReceived(data));
        }
        dispatch(permissionsLoading(false));
      })
      .catch((err) => {
        dispatch(permissionsLoading(false));
      });
  };

  const isDisabled =
    fetching || !isSurveyAdmin() || survey.status == SURVEY_STATUS.CLOSED;

  return (
    <Box className={styles.mainContainer}>
      <>
        <Stack
          flexDirection="row"
          alignItems="bottom"
          sx={{ marginBottom: `16px` }}
        >
          <FormControlLabel
            disabled={isDisabled}
            control={<Checkbox checked={checked} onChange={onChangeCheckbox} />}
            label={t("label.apply_quota")}
          />
          <TextField
            variant="standard"
            type="number"
            sx={{ width: 200 }}
            disabled={!checked || isDisabled}
            label={t("label.total_responses_limit")}
            value={limit}
            onChange={(e) => {
              changeLimit(e.target.value);
            }}
          />
        </Stack>

        <Box>
          <Typography sx={{ mb: "10px", fontSize: "20px" }}>
            {t("edit_survey.limit_responses")}
          </Typography>
          <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} aria-label="simple table">
              <TableHead>
                <TableRow>
                  <TableCell align="left">
                    {t("edit_survey.surveyor")}
                  </TableCell>
                  <TableCell align="left">{t("users_manage.roles")}</TableCell>
                  <TableCell align="left">{t("edit_survey.count")}</TableCell>
                  <TableCell align="left">{t("edit_survey.limit")}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {permissionAll?.map((permission) => {
                  return (
                    <TableRow key={permission.user.id}>
                      <TableCell align="left">
                        {permission.user.firstName} {permission.user.lastName}
                      </TableCell>
                      <TableCell align="left">
                        {permission.user.roles
                          .map((role) => t(`label.${role}`))
                          .join(", ")}
                      </TableCell>
                      <TableCell align="left">{permission.count}</TableCell>
                      <TableCell align="left">
                        <UserQuota
                          t={t}
                          quota={permission.quota}
                          isDisabled={isDisabled}
                          onSubmit={(quota) => {
                            putPermissionBySurveyor(permission.user.id, quota);
                          }}
                        />
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </TableContainer>
        </Box>
      </>

      <Backdrop open={fetching} sx={{ position: "absolute" }}>
        <CircularProgress />
      </Backdrop>
    </Box>
  );
}

function UserQuota({ t, isDisabled, quota, onSubmit }) {
  const [checked, setChecked] = useState(quota > 0);
  const [limit, setLimit] = useState(quota >= 0 ? quota : "");
  const onChangeCheckbox = (e) => {
    if (!e.target.checked) {
      setLimit("");
    }
    setChecked(e.target.checked);
  };

  const changeLimit = (value) => {
    const intValue = parseInt(value);
    if (Number.isInteger(intValue) && intValue >= 0) {
      setLimit(intValue);
    }
  };
  return (
    <>
      <FormControlLabel
        disabled={isDisabled}
        control={<Checkbox checked={checked} onChange={onChangeCheckbox} />}
        label={t("label.apply_quota")}
      />
      <Stack flexDirection="row" alignItems="bottom">
        <TextField
          type="number"
          variant="standard"
          label={t("label.responses_limit")}
          disabled={!checked || isDisabled}
          value={limit}
          onChange={(e) => {
            changeLimit(e.target.value);
          }}
        />
        <Button
          onClick={() => onSubmit(limit == "" ? -1 : limit)}
          disabled={(limit == "" && quota < 0) || limit == quota}
        >
          {t("action_btn.save")}
        </Button>
      </Stack>
    </>
  );
}

export default SurveyQuota;
