import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector, useDispatch } from "react-redux";
import {
  Box,
  FormControlLabel,
  Checkbox,
  FormControl,
  Paper,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  IconButton,
  CircularProgress,
  Button,
  Backdrop,
} from "@mui/material";
import { Delete } from "@mui/icons-material";
import {
  onError,
  permissionsLoading,
  permissionsReceived,
  surveyAttributeChanged,
} from "~/state/edit/editState";
import { UserDelete } from "~/components/users/UserDelete";
import { SURVEY, SURVEY_STATUS } from "~/constants/survey";
import { isSurveyAdmin } from "~/constants/roles";
import SurveyService from "~/services/SurveyService";
import { PROCESSED_ERRORS, processError } from "~/utils/errorsProcessor";
import { AddPermission } from "../AddPermission";
import styles from "./SurveyUsers.module.css";

function SurveyUsers() {
  const dispatch = useDispatch();
  const { t } = useTranslation("manage");
  const survey = useSelector((state) => state.editState.survey);
  const fetching = useSelector((state) => state.editState.permissionsLoading);
  const permissionAll = useSelector((state) => state.editState.permissions);
  const isDisabled =
    fetching || !isSurveyAdmin() || survey.status == SURVEY_STATUS.CLOSED;

  const onChangeCheckbox = (e) => {
    dispatch(
      surveyAttributeChanged({
        key: e.target.name,
        value: e.target.checked,
      })
    );
  };



  const loadPermissions = (surveyId) => {
    SurveyService.getSurveyPermissionAll(surveyId)
      .then((data) => {
        if (data) {
          dispatch(permissionsReceived(data));
        }
        dispatch(permissionsLoading(false));
      })
      .catch((err) => {
        processApirror(err)
      });
  };

  const [userToDelete, setUserToDelete] = useState(null);
  const onCloseModal = () => {
    setUserToDelete(null);
  };

  const [addPermission, setAddPermission] = useState(false);
  const onCloseModalAddPermission = () => {
    setAddPermission(false);
  };

  const processApirror = (e) => {
    dispatch(permissionsLoading(false));
    const processed = processError(e);
    switch (processed) {
      case PROCESSED_ERRORS.NETWORK_ERR:
      case PROCESSED_ERRORS.BACKEND_DOWN:
        dispatch(onError(processed));
        break;
      default:
        break;
    }
  };

  const deleteUser = () => {
    dispatch(permissionsLoading(true));
    onCloseModal();
    SurveyService.removePermission(survey.id, userToDelete.id)
      .then(() => {
        loadPermissions(survey.id);
      })
      .catch((e) => {
        processApirror(e);
      });
  };

  const postPermission = (userId) => {
    onCloseModalAddPermission();
    dispatch(permissionsLoading(true));
    SurveyService.addPermission(survey.id, userId)
      .then(() => {
        loadPermissions(survey.id);
      })
      .catch((e) => {
        processApirror(e);
      });
  };

  return (
    <Box className={styles.mainContainer}>
      <FormControl sx={{ mb: "30px" }}>
        <FormControlLabel
          control={
            <Checkbox
              disabled={isDisabled}
              name={SURVEY.PUBLIC_WITHIN_ORG}
              checked={survey.publicWithinOrg}
              onChange={onChangeCheckbox}
            />
          }
          label={t("label.public_within_org")}
        />
      </FormControl>
      <Button
        disabled={isDisabled}
        sx={{ ml: "auto", mb: "10px", display: "flex" }}
        onClick={() => {
          setAddPermission(true);
        }}
      >
        {t("action_btn.add_permission")}
      </Button>

      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell align="left">{t("users_manage.name")}</TableCell>
              <TableCell align="left">{t("users_manage.email")}</TableCell>
              <TableCell align="left">{t("users_manage.roles")}</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {permissionAll?.map(({ user }) => {
              return (
                <TableRow key={user.id}>
                  <TableCell component="th" scope="row">
                    <IconButton
                      title={t("users_manage.title_delete")}
                      disabled={isDisabled}
                      onClick={() => {
                        setUserToDelete(user);
                      }}
                    >
                      <Delete />
                    </IconButton>
                  </TableCell>
                  <TableCell align="left">
                    {user.firstName} {user.lastName}
                  </TableCell>
                  <TableCell align="left">{user.email}</TableCell>
                  <TableCell align="left">
                    {user.roles.map((role) => t(`label.${role}`)).join(", ")}
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>

      {Boolean(userToDelete) && (
        <UserDelete
          deleteUser={deleteUser}
          name={userToDelete.firstName + " " + userToDelete.lastName}
          open={Boolean(userToDelete)}
          onClose={onCloseModal}
        />
      )}
      {addPermission && (
        <AddPermission
          currentUserIds={permissionAll.map((permission) => permission.user.id)}
          addPermission={postPermission}
          open={addPermission}
          onClose={onCloseModalAddPermission}
        />
      )}
      <Backdrop open={fetching} sx={{ position: "absolute" }}>
        <CircularProgress />
      </Backdrop>
    </Box>
  );
}

export default SurveyUsers;
