import React from "react";
import { useTranslation } from "react-i18next";
import { useSelector, useDispatch } from "react-redux";
import { Box, FormControlLabel, Checkbox, FormControl } from "@mui/material";
import { surveyAttributeChanged } from "~/state/edit/editState";
import { SURVEY, SURVEY_STATUS } from "~/constants/survey";
import styles from "./SurveyOffline.module.css";
import { isSurveyAdmin } from "~/constants/roles";

function SurveyOffline() {
  const dispatch = useDispatch();
  const { t } = useTranslation("manage");
  const survey = useSelector((state) => state.editState.survey);
  const isDisabled = !isSurveyAdmin() || survey.status == SURVEY_STATUS.CLOSED;

  const onChangeCheckbox = (e) => {
    dispatch(
      surveyAttributeChanged({
        key: e.target.name,
        value: e.target.checked,
      })
    );
  };

  return (
    <Box className={styles.mainContainer}>
      <FormControl className={styles.formControlCheckBox}>
        <FormControlLabel
          control={
            <Checkbox
              disabled={isDisabled}
              name={SURVEY.BACKGROUND_AUDIO}
              checked={survey.backgroundAudio}
              onChange={onChangeCheckbox}
            />
          }
          label={t("edit_survey.background_audio")}
        />
        <FormControlLabel
          control={
            <Checkbox
              disabled={isDisabled}
              name={SURVEY.RECORD_GPS}
              checked={survey.recordGps}
              onChange={onChangeCheckbox}
            />
          }
          label={t("edit_survey.record_gps")}
        />
        <FormControlLabel
          control={
            <Checkbox
              disabled={isDisabled}
              name={SURVEY.CAN_LOCK_SURVEY}
              checked={survey.canLockSurvey}
              onChange={onChangeCheckbox}
            />
          }
          label={t("edit_survey.can_lock_survey")}
        />
      </FormControl>
    </Box>
  );
}

export default SurveyOffline;
