import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { Box, Button, TextField, FormHelperText, Stack } from "@mui/material";
import ProcessedError from "~/components/manage/ProcessedError";
import LoadingIndicator from "~/components/common/LoadingIndicator";
import SurveyService from "~/services/SurveyService";
import { AdditionalLanguages } from "../AdditionalLanguages";
import { SurveyActiveFromTo } from "../SurveyActiveFromTo";
import { PROCESSED_ERRORS, processError } from "~/utils/errorsProcessor";
import { isSurveyAdmin } from "~/constants/roles";
import { routes } from "~/routes";
import styles from "./EditSurveyGeneral.module.css";
import { SURVEY_STATUS } from "~/constants/survey";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import {
  surveyAttributeChanged,
  surveyAttributeChangedImmediate,
  surveyReceived,
} from "~/state/edit/editState";
import { localDateToServerDateTime } from "~/utils/DateUtils";
import { SurveyClone } from "../SurveyClone";

function EditSurveyGeneral() {
  const navigate = useNavigate();
  const { t } = useTranslation("manage");
  const survey = useSelector((state) => state.editState.survey);
  const surveyNameError = useSelector(
    (state) => state.editState.error == PROCESSED_ERRORS.DUPLICATE_SURVEY_NAME
  );
  const [processedError, setProcessedError] = useState("");
  const [surveyName, setSurveyName] = useState(survey.name);
  const [surveyDateError, setSurveyDateError] = useState("");
  const [showError, setShowError] = useState(false);
  const [loading, setLoading] = useState(false);

  const onSurveyNameChanged = (e) => {
    setSurveyName(e.target.value);
  };

  const dispatch = useDispatch();

  const isDisabled = !isSurveyAdmin() || survey.status == SURVEY_STATUS.CLOSED;

  const saveSurveyName = () => {
    dispatch(
      surveyAttributeChangedImmediate({ key: "name", value: surveyName })
    );
  };

  const onAdditionalLanguagesChanged = (e) => {
    const key = "additionalLanguages";
    if (e.target.checked) {
      dispatch(
        surveyAttributeChanged({
          key,
          value: [
            ...survey.additionalLanguages,
            { code: e.target.name, langName: "" },
          ],
        })
      );
    } else {
      dispatch(
        surveyAttributeChanged({
          key,
          value: [
            ...survey.additionalLanguages.filter(
              (language) => language.code !== e.target.name
            ),
          ],
        })
      );
    }
  };

  const onSurveyActiveFromChanged = (value) => {
    setSurveyDateError("");
    dispatch(
      surveyAttributeChanged({
        key: "startDate",
        value: localDateToServerDateTime(value.toDate()),
      })
    );
  };

  const onSurveyActiveToChanged = (value) => {
    setSurveyDateError("");
    dispatch(
      surveyAttributeChanged({
        key: "endDate",
        value: localDateToServerDateTime(value.toDate()),
      })
    );
  };

  const goDashboard = () => {
    navigate(routes.dashboard);
  };

  const closeSurvey = () => {
    setLoading(true);
    SurveyService.closeSurvey(survey.id)
      .then((data) => {
        dispatch(surveyReceived(data));
      })
      .catch((e) => {
        const processed = processError(e);
        switch (processed) {
          case PROCESSED_ERRORS.NETWORK_ERR:
          case PROCESSED_ERRORS.BACKEND_DOWN:
            setShowError(true);
            setProcessedError(processed);
            break;
          case PROCESSED_ERRORS.INVALID_SURVEY_DATES:
            setSurveyDateError(t(`processed_errors.${processed}`));
          default:
            break;
        }
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const onDelete = () => {
    if (
      window.confirm(
        t("edit_survey.delete_survey", { survey_name: survey.name })
      )
    ) {
      deleteSurvey(survey.id);
    }
  };

  const deleteSurvey = () => {
    setLoading(true);
    SurveyService.deleteSurvey(survey.id)
      .then(() => {
        goDashboard();
      })
      .catch((e) => {
        const processed = processError(e);
        switch (processed) {
          case PROCESSED_ERRORS.NETWORK_ERR:
          case PROCESSED_ERRORS.BACKEND_DOWN:
            setShowError(true);
            setProcessedError(processed);
            break;
          default:
            break;
        }
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const [openSurveyClone, setOpenSurveyClone] = useState(false);

  return (
    <Box className={styles.mainContainer}>
      {loading && <LoadingIndicator />}
      {showError && (
        <ProcessedError
          error={processedError}
          handleClose={() => setShowError(false)}
        />
      )}
      <Box className={styles.createUserCard}>
        <TextField
          disabled={isDisabled}
          sx={{ maxWidth: "400px" }}
          error={surveyNameError}
          required={true}
          value={surveyName}
          id="surveyName"
          label={t("label.survey_name")}
          name="surveyName"
          onChange={onSurveyNameChanged}
          variant="standard"
        />

        <Button
          onClick={saveSurveyName}
          disabled={survey.name == surveyName}
          className={styles.saveNameButton}
        >
          {t("action_btn.save")}
        </Button>
        {surveyNameError && (
          <FormHelperText className={styles.errorText}>
            {t("processed_errors.duplicate_survey_name")}
          </FormHelperText>
        )}
        <Stack flexDirection="row" sx={{ mt: "40px" }}>
          <Box className={styles.blockItem}>
            <Box>
              <Box component="span" sx={{ fontWeight: 700 }}>
                {t("label.base_language")}:&nbsp;
              </Box>
              {survey.defaultLanguage.langName}
            </Box>

            <Box>
              <Box component="span" sx={{ fontWeight: 700 }}>
                {t("label.survey_mode")}:&nbsp;
              </Box>
              {t(`mode.${survey.usage}`)}
            </Box>

            <Box>
              <Box component="span" sx={{ fontWeight: 700 }}>
                {t("edit_survey.status")}:&nbsp;
              </Box>
              {t(`status.${survey.status}`)}
            </Box>
            <br />

            <Box sx={{ display: "flex", gap: "10px" }}>
              {survey.status == SURVEY_STATUS.ACTIVE && (
                <Button
                  disabled={isDisabled}
                  onClick={closeSurvey}
                  sx={{ border: "1px solid green", color: "#3c3c3c" }}
                >
                  {t("action_btn.close")}
                </Button>
              )}
              <Button
                onClick={() => {
                  setOpenSurveyClone(true);
                }}
                sx={{ border: "1px solid blue", color: "#3c3c3c" }}
              >
                {t("action_btn.clone")}
              </Button>
              {survey.status != SURVEY_STATUS.ACTIVE && (
                <Button
                  onClick={onDelete}
                  sx={{ border: "1px solid red", color: "#3c3c3c" }}
                >
                  {t("action_btn.delete")}
                </Button>
              )}
            </Box>
          </Box>
          <Box className={styles.blockItem}>
            <AdditionalLanguages
              disabled={isDisabled}
              onAdditionalLanguagesChanged={onAdditionalLanguagesChanged}
              additionalLanguages={survey.additionalLanguages.map(
                (lang) => lang.code
              )}
              baseLanguage={survey.defaultLanguage.code}
            />
            <br />

            <SurveyActiveFromTo
              error={surveyDateError}
              disabled={isDisabled}
              surveyActiveFrom={survey.startDate}
              surveyActiveTo={survey.endDate}
              onSurveyActiveFromChanged={onSurveyActiveFromChanged}
              onSurveyActiveToChanged={onSurveyActiveToChanged}
            />
          </Box>
        </Stack>
      </Box>
      <SurveyClone
        open={openSurveyClone}
        onClose={() => {
          setOpenSurveyClone(false);
        }}
      />
    </Box>
  );
}

export default EditSurveyGeneral;
